.main-container {
  padding: 50px $container-horiz-padding;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  width: $container-width;
  &__card {
    margin-right: $card-marging-right;
  }

  &__card:nth-child(4n) {
    margin-right: 0;
  }

  &__card:nth-last-child(-n + 4) {
    margin-top: 46px;
  }
}
